@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.social{
    margin-top: 10px;
}

.social a {
    font-size: 2em;
    margin-right: 10px;
    cursor: pointer;
}

.social a svg{
    transition: all 0.2s linear;
}

.social a svg:hover{
    transform: scale(1.05);
    color:#7fd4d3;
}

.main {
  align-items: center;
  margin-top: 33vh;
  margin-bottom: 33vh;
}
  
.main-text {
  white-space: pre-wrap;
  font-size: 3em;
  margin: 0;
}

.about-con{
    padding-right: 17vw;
    padding-left: 17vw;
}
.about-con section {
    display: flex;
    flex-direction: column;
}

.about-me{
    display: flex;
    justify-content: space-between;
}
.about-me-text{
    width:70%;
    padding-right: 5%;
}
.about h1{
    font-size: 4em;
    margin-bottom: 1rem;
}
.about-me-intro{
    font-size: 1.6rem;
}
.about-me-desc{
    font-size: 1.3rem;
    font-weight: 300;
}
.about-me-img{
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill-wrap {
    display: flex;
    height: 65px;
    margin-top: 20px;
}

.skill {
    display: flex;
    flex-direction: column;
    width: 160px;
    margin-right: 15px;
}
.skill-title {
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill-name {
    margin-left: 5px;
    font-weight: 300;
    font-size: 1rem;
}
.skill-bar{
    display: flex;
}

.skill-bar > div {
    width: 15px;
    height: 15px;
    margin-right: 1px;
    background: rgba(0, 0, 0, 0.15);
}
/*############################## START OF HEADER STYLE */
.header ul {
    padding: 0;
    display: flex; 
    justify-content: space-between;
  }
  
  .header ul li {
    display: flex; 
    align-items: center;
  }
  
  .header ul li a {
    transition: all 0.2s ease-in-out;
    position: relative;
  }
  
  .header ul li a::after {
    background-color: #7fd4d3;
    bottom: -1px;
    content: ' ';
    display: block;
    height: 12px;
    width: 100%;
    left: 7px;
    opacity: 0.5;
    position: absolute;
    transition: all 0.2s ease-in-out;
    z-index: -1;
  }
  
  .header ul li a:hover::after{
    width: 120%;
    opacity: 1;
    left: 2px;
  }
  
  /*############################## END OF HEADER STYLE */
