html, body {
  min-height: 100%;
}

body {
  background-color: #F8F8F8;
  font-family: 'Lato', monospace, Helvetica, Arial, sans-serif;
  font-size: 1em;
  height: 100%;
}

a {
  text-decoration: none;
  color: #333;
}

li {
  list-style-type: none;
}

.dep1-root {
  padding-right: 5vw;
  padding-left: 5vw;
}

main{
  display: flex;
  flex-direction: column;
  -webkit-animation: main-ani 1.2s ease-in forwards;
          animation: main-ani 1.2s ease-in forwards;
}

@-webkit-keyframes main-ani {
  from{
    opacity: 0;
    transform: translateY(0);
  }to{
    opacity: 1;
    transform: translateY(10px);
  }
}

@keyframes main-ani {
  from{
    opacity: 0;
    transform: translateY(0);
  }to{
    opacity: 1;
    transform: translateY(10px);
  }
}

